.dashboard_main{
    position: relative;
}

.mint-box{
    background-color: rgba(255,255,255,0.2);
    padding: 40px;
    border-radius: 10px;
}

.minting_box {
    position: relative;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 25px;
    // border-radius: 15px;
    // border: 2px dashed #fff;
    box-shadow: 6px 6px 15px 5px rgba(255, 255, 255, .2);
    text-align: center;
}


.minting_box *{
    font-family: 'Arvo', serif;
    text-align: center;
    font-size: 20px;
    margin-bottom: 0px;
    color: #fff;
}

.socialLinks{
    width: 100%;
    text-align: center;
}

.socialLinks svg{
    height: 40px;
    margin-top: -10px;
    width: 60px;
}

.Nanum-font{
    font-family: 'Nanum Pen Script', cursive;
}

p.Nanum-font{
    font-size: 34px;
}

h1.Nanum-font{
    font-size: 55px;
    font-weight: 700;
    line-height: 30px;
    margin-top: 12px;
}

h1.Nanum-font span{
    font-family: 'Nanum Pen Script', cursive;
    font-size: 26px;
    font-weight: 500;
    line-height: 31px;
}

.socialLinks span{
    font-family: 'Nanum Pen Script', cursive;
    font-size: 30px;
    font-weight: bold;
    color: #fff;
}

.minting_box .card{
    background-color: transparent;
    border:20px solid transparent;
    padding:20px;
    // margin-bottom:20px;
    -webkit-border-image:url(../../assets/frame-leaf.gif) 22 repeat; /* Safari */
    -o-border-image:url(../../assets/frame-leaf.gif) 22 repeat; /* Opera */
    border-image:url(../../assets/frame-leaf.gif) 22 repeat;
    display: inline-block;
    width: 100%;
    margin-top: 30px;
}

.dashboard_main>.container>.row{
    height: calc(100vh - 195px);
}

.claim_btn{
    background:transparent;
    border: none;
    display: inline-block;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    cursor: pointer;
    padding: 0px 40px;
    color: #fff;
    font-weight: bold;
    border: 3px solid #fff;
    font-family: 'Nanum Pen Script', cursive;
    font-size: 34px;
}

.footerText{
    font-size: 20px;
    text-align: center;
    font-family: 'Arvo', serif;
    color: #fff;
}

.footerText a{
    color: #f3b357;
}

.minting_form {
    display: flex;
    justify-content: center;
}

.ml-30{
    margin-left: 30px;
}

.minting-box{
    margin: 25px 0 0px 0;
}

.minting_form button {
    padding: 4px 8px;
    border-radius: 6px;
    margin: 0 10px;
    border: 3px solid #fff;
    background: transparent;
    color: #fff;
}

.minting_form button img {
    width: 34px;
    filter: invert(1);
}

.minting_form span {
    padding: 8px 17px;
    border-radius: 4px;
    border: 0;
    color: #000;
    font-weight: 700;
    background: #fff;
    min-width: 200px;
    text-align: center;
    font-size: 20px;
    line-height: 42px;
}

.mt-25{
    margin-top: 25px;
}

.minting-box h3{
    font-family: 'Nanum Pen Script', cursive;
    font-size: 58px;
    font-weight: bold;
    margin-right: 30px;
    line-height: 54px;
}

div.alert{
    text-align: center;
    font-family: 'Nanum Pen Script', cursive;
    font-size: 30px;
    padding: 2px 20px;
}

.w-85{
    width: 85%;
}