@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo&family=Fredoka+One&display=swap);
body{background-color:#000 !important;background-image:url(/static/media/bg-01.png);background-repeat:no-repeat;background-size:cover;background-attachment:fixed;background-position:bottom}h1,h2,h3,h4,h5,h6{color:#fff !important}

@font-face{font-family:'Montserrat', sans-serif;src:url(/static/media/Montserrat-Regular.3cd78665.ttf) format("ttf");font-weight:400;font-style:normal;font-display:swap}@font-face{font-family:'Montserrat', sans-serif;src:url(/static/media/Montserrat-Medium.e2d60bc4.ttf) format("ttf");font-weight:500;font-style:normal;font-display:swap}@font-face{font-family:'Montserrat', sans-serif;src:url(/static/media/Montserrat-SemiBold.fa8441f3.ttf) format("ttf");font-weight:600;font-style:normal;font-display:swap}@font-face{font-family:'Montserrat', sans-serif;src:url(/static/media/Montserrat-Bold.079ca05d.ttf) format("ttf");font-weight:700;font-style:normal;font-display:swap}@font-face{font-family:'Montserrat', sans-serif;src:url(/static/media/Montserrat-ExtraBold.1944c8d1.ttf) format("ttf");font-weight:800;font-style:normal;font-display:swap}

.dashboard_main{position:relative}.mint-box{background-color:rgba(255,255,255,0.2);padding:40px;border-radius:10px}.minting_box{position:relative;background-color:rgba(0,0,0,0.5);padding:25px;box-shadow:6px 6px 15px 5px rgba(255,255,255,0.2);text-align:center}.minting_box *{font-family:'Arvo', serif;text-align:center;font-size:20px;margin-bottom:0px;color:#fff}.socialLinks{width:100%;text-align:center}.socialLinks svg{height:40px;margin-top:-10px;width:60px}.Nanum-font{font-family:'Nanum Pen Script', cursive}p.Nanum-font{font-size:34px}h1.Nanum-font{font-size:55px;font-weight:700;line-height:30px;margin-top:12px}h1.Nanum-font span{font-family:'Nanum Pen Script', cursive;font-size:26px;font-weight:500;line-height:31px}.socialLinks span{font-family:'Nanum Pen Script', cursive;font-size:30px;font-weight:bold;color:#fff}.minting_box .card{background-color:transparent;border:20px solid transparent;padding:20px;border-image:url(data:image/gif;base64,) 22 repeat;display:inline-block;width:100%;margin-top:30px}.dashboard_main>.container>.row{height:calc(100vh - 195px)}.claim_btn{background:transparent;border:none;display:inline-block;border-radius:5px;cursor:pointer;padding:0px 40px;color:#fff;font-weight:bold;border:3px solid #fff;font-family:'Nanum Pen Script', cursive;font-size:34px}.footerText{font-size:20px;text-align:center;font-family:'Arvo', serif;color:#fff}.footerText a{color:#f3b357}.minting_form{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.ml-30{margin-left:30px}.minting-box{margin:25px 0 0px 0}.minting_form button{padding:4px 8px;border-radius:6px;margin:0 10px;border:3px solid #fff;background:transparent;color:#fff}.minting_form button img{width:34px;-webkit-filter:invert(1);filter:invert(1)}.minting_form span{padding:8px 17px;border-radius:4px;border:0;color:#000;font-weight:700;background:#fff;min-width:200px;text-align:center;font-size:20px;line-height:42px}.mt-25{margin-top:25px}.minting-box h3{font-family:'Nanum Pen Script', cursive;font-size:58px;font-weight:bold;margin-right:30px;line-height:54px}div.alert{text-align:center;font-family:'Nanum Pen Script', cursive;font-size:30px;padding:2px 20px}.w-85{width:85%}

.opensea_available{background-color:#fff;font-weight:600;letter-spacing:0.4px;text-align:center;font-size:12px;padding:3px 0px;margin-bottom:0px}.opensea_available a{color:#000}

#myNav{background:transparent;width:100%;z-index:99;padding:14px 16px}.navbar-brand,.navbar-brand:hover{font-family:'Nanum Pen Script', cursive;font-size:60px;font-weight:700;text-transform:uppercase;color:#fff;line-height:60px}.nav-link{font-family:'Nanum Pen Script', cursive}.navbar-brand img{max-width:180px}#myNav ul{-webkit-align-items:center;align-items:center}.ml-auto,.mx-auto{margin-left:auto !important}#myNav ul li a{font-size:30px !important;padding:0px 18px;color:#fff;text-transform:uppercase;font-size:15px;text-align:right;padding:0 30px;position:relative;letter-spacing:1px}.nav-item{position:relative}#myNav ul li a{font-weight:bold;transition:1s}#myNav ul li:hover>a{-webkit-transform:rotatey(360deg) scale(1.2);transform:rotatey(360deg) scale(1.2)}.nav-item .claim_btn{font-size:28px !important}

