#myNav {
    background: transparent;
    width: 100%;
    z-index: 99;
    padding: 14px 16px;
}

.navbar-brand, .navbar-brand:hover{
    font-family: 'Nanum Pen Script', cursive;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;   
    color: #fff;
    line-height: 60px; 
}

.nav-link{
    font-family: 'Nanum Pen Script', cursive;
}


.navbar-brand img {
    max-width: 180px;
}

#myNav ul {
    align-items: center;
}

.ml-auto, .mx-auto {
    margin-left: auto!important;
}

#myNav ul li a {
    font-size: 30px !important;
    padding: 0px 18px;
    color: #fff;
    text-transform: uppercase;
    font-size: 15px;
    text-align: right;
    padding: 0 30px;
    position: relative;
    letter-spacing: 1px;
}

.nav-item{
    position:relative;
}

#myNav ul li a{
    font-weight:bold;
    -webkit-transition:1s;
    -moz-transition:1s;
    transition:1s;
}
// #myNav ul li a:hover{
//    box-shadow:0 -30px 0 0 rgba(0,0,0,0.5) inset; 
// }
#myNav ul li:hover > a{
    -webkit-transform:rotatey(360deg) scale(1.2);
    -moz-transform:rotatey(360deg) scale(1.2);
    transform:rotatey(360deg) scale(1.2);
}

.nav-item .claim_btn{
    font-size: 28px !important;
}